nav {
  background-color: #333;
  overflow: hidden
}

nav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

nav a:hover {
  background-color: #ddd;
  color: black;
}

nav a:active {
  background-color: #04AA6D;
  color: white;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial;
  margin: 0;
}

header {
  padding: 1px;
  text-align: center;
  background: #37a08b;
  color: white;
  font-size: 15px;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  text-align: left;
  padding: 8px;
}

tr:nth-child(even){background-color: #f2f2f2}

th {
  background-color: #04AA6D;
  color: white;
}

td button {
  background-color: #4CAF50; /* Green */
  border: 30px;
  border-radius: 2px;
  color: white;
  text-decoration: none;
  font-size: 15px;
}

.customer-form {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.customer-form input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.customer-form label {
  text-align: left;
}

.edit-form button {
  background-color: #4CAF50; 
  border: none;
  color: white;
  padding: 15px 32px;
  margin-left: 50px;
  margin-top: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
}
.submit {
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.submit:hover {
  background-color: #45a049;
}

#lastName {
  float: center;
  padding: 2px;
  border: 0px;
  margin-top: 0px;
  margin-right: 0px;
  font-size: 17px;
  border-width: 20px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
